$header-height: 170px;
$header-laptop-height: 110px;
$header-tablet-height: 70px;
$header-mobile-height: 40px;
$header-shrink-height: 70px;

$box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

$colors: (
    'blue': #4267b2,
    'orange': #fbb96b,
    'pink': #fe7e73,
    'white': #fff,
    'black': #000,
    'red': #ff0000,
    'yellow': #ffee00,
    'main': $main-color,
